.page-header__project{
  display: flex;
  align-items: center;
  gap: 12px;
}
.link{
  text-decoration: none;
  color: #FAFAFE; 
  display: flex;
  align-items: center;
  padding: 6px;
  gap: 12px;
}
.link h1{
  font-size: 14px;
}

.link:hover{
  background-color: #262837;
  border-radius: 4px;
}

.page-header__project input{
  padding-left: .5em;
  color: #7F7D8E;
  background: transparent;
  border: none; 
  font-size: 14px; 
  cursor: pointer;
  outline: none;
  width: 150px; 
  /* width: fit-content;  */
  /* background-color: rgba(255, 255, 0, 0.4); */
}


.container{
  position: relative; 
}
.clickable{
  cursor: pointer;
  /* opacity: 0.4;
  background-color: red;  */
  position: absolute; 
  width:100%; 
  height:100%; 
}
.clickable:hover{
  outline-offset: 5px;
  outline:1px solid #37394b;
  border-radius: 2px;
}

.hidden{
  display: none; 
}